@font-face {
  font-family: 'Homer_Simpson_Revised';
  src: local('Homer_Simpson_Revised'), url(./fonts/Homer_Simpson_Revised.ttf) format('truetype');
}

body {
  margin: 0;
  padding: 0;
  font-family: "Homer_Simpson_Revised", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
